.video-container {
    width: 100%;
}

.video-container .OTPublisherContainer {
    width: 100px !important;
    height: 75px !important;
    position: absolute !important;
    bottom: 0 !important;
    margin-left: calc(100% - 100px) !important;
}

.subscriber-not-connected .OTPublisherContainer {
    width: 100% !important;
    height: 100vmin !important;
}

.video-button-container {
    position: absolute;
    z-index: 999;
    bottom: 10px;
    width: 100%;
    text-align: center;
}

.waiting-message-container{
    position: absolute !important;
    z-index: 999;
    top: 50px;
    text-align: center;
}

.transparent-background {
    background: rgba(0, 0, 0, 0.5) !important;
}

.red-background {
    background: red !important;
}

.margin-horizontal-10 {
    margin-left: 10px;
    margin-right: 10px;
}

.OT_edge-bar-item {
    display: none !important;
}

.OTSubscriberContainer {
    /* width: 100% !important;
    height: 100vh !important; */

    width: 100% !important;
    height: 100vmin !important;
}

.recording-icon {
    z-index: 9999;
    background-color: #ff0000;
    left: 10px;
    top: 60px;
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 5px;
}

.OT_publisher, .OT_subscriber{
    position: inherit !important;
}
