.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.-sort-asc {
    box-shadow: inset 0 3px 0 0 #1890ff !important;
}

/* New CSS */

body {
    /* margin: 0; */
    font-family: Nunito, sans-serif !important;
    /* font-size: 1.275rem; */
    /* font-weight: 400 !important; */
    /* line-height: 1.5; */
    /* color: #6c757d; */
    /* text-align: left; */
    background-color: #f5f6fa !important;
    height: 100% !important;
    background: #f5f6fa !important;
}

.page-content, .sivotec-page-content {
    background: #f5f6fa !important;
    background-color: #f5f6fa !important;
}

.react-tel-input {
    margin-top: 4px !important;
}

.react-tel-input .form-control{
    width: 100% !important;
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    border-bottom: 1px solid rgb(38, 50, 56) !important;
    border-radius: 0 !important;
}

.react-app-card-container{
    background-color: #fff !important;
    margin-top: 2%;
    border-top: 2px #1890ff solid !important;
    margin-bottom: 10px;
    padding-top: 10px !important;
    border-radius: 5px;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
}

.boxShadow{
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12) !important;
}
.ant-form-item-label{
    line-height: normal !important;
    margin-top: 10px;
}

.ReactTable .rt-table .rt-thead{
    background-color: #f5f6fa !important;
    font-size: 15px;
    padding: 7px 0px;
    font-weight: 500;

}

.ReactTable .rt-table .rt-tbody .rt-td{
    padding: 2px 5px;
}

.MuiToolbar-regular {
    min-height: 48px !important;
}

.MuiInputLabel-outlined[data-shrink="false"] {
    z-index: 1;
    transform: translate(14px, 13px) scale(1) !important;
    pointer-events: none;
}

.MuiInputBase-input.MuiOutlinedInput-input{
    font-size: 14px;
    letter-spacing: .01rem;
    padding: 10px 10px !important;
}

.react-tel-input .form-control+div:before {
    content: 'Phone *' !important;
    text-align: left;
    font-size: 11px !important;
    width: 49px !important;
    margin-left: -25px;
    /* margin-top: 10px; */
}

.react-tel-input .form-control {
    padding-bottom: 10px !important;
    padding-top: 10px !important;
    font-size: 14px !important;
    /* margin-top: 10px; */
}

.MuiInputBase-root.MuiInput-root input,
.MuiInputBase-root.MuiInput-root textarea,
{
    border: 1px solid #CACACA;
    border-radius: 5px;
    /* line-height: 25px; */
    font-size: 14px;
    letter-spacing: .01rem;
    padding: 10px 10px !important;
    /* height: 30px; */
    width: 100% !important;
}

.MuiAutocomplete-inputRoot .MuiInputBase-input.MuiOutlinedInput-input{
    padding: 3px !important;
}

.MuiFormControl-root.MuiTextField-root.MuiFormControl-fullWidth{
    /* margin-top: 10px; */
}
.MuiInputBase-root.MuiInput-root.materialSelect{
    width: 100%;
}
.MuiInputBase-root.MuiInput-root.materialSelect .MuiSelect-root.MuiSelect-select{
    padding: 1px 5px !important;
    min-height: 30px !important;
}

.materialSelect .MuiChip-root{
    height: 25px !important;
}

.MuiInput-underline:before, .MuiInput-underline:after{
    /* border-bottom: none !important; */
}

.MuiFormLabel-root.MuiInputLabel-root.MuiInputLabel-formControl.Mui-focused.MuiInputLabel-shrink{
    padding: 0px;
    font-size: 14px !important;
}

.MuiTableCell-root{
    padding: 8px 16px !important;
}

.MuiSelect-select.MuiTablePagination-select div{
    margin-right: 5px !important;
}

.sideDrawer div::-webkit-scrollbar-thumb {
    background: #2C78F8;;
    border-radius: 20px;
}

.sideDrawer div::-webkit-scrollbar {
    width: 8px;
    background-color: #F5F5F5;
}

.marginTop10{
    margin-top: 10px !important;
}

.MuiSnackbar-anchorOriginTopRight {
    top: 50px !important;
}

.errorMessage{
    color: #e53935 !important;
    margin-bottom: 10px !important;
    font-size: 11px;
    margin-left: 14px;
    margin-right: 14px;
}

.MuiBottomNavigationAction-label.Mui-selected{
    cursor:pointer;
    color:#2C78F8;
    text-decoration:underline;
}

.MuiBottomNavigationAction-label.Mui-selected:hover {
    text-decoration:none;
    /* text-shadow: 1px 1px 1px #555; */
}

.MuiBottomNavigationAction-root.Mui-selected {
    color: #2C78F8;
    padding-top: 0px !important;
}

.MuiBottomNavigationAction-root{
    padding: 0px 0px 0px;
}

.MuiBottomNavigation-root{
    height: 40px !important;
    margin-top: 10px;
}

.MuiButton-containedPrimary {
    color: #FFFFFF;
    background-color: #4194F2 !important;
}

.MuiButton-outlinedPrimary {
    color: #4194F2 !important;
    border: 1px solid #4194F2 !important;
}

.MuiChip-outlinedPrimary {
    color: #4194F2 !important;
    border: 1px solid #4194F2 !important;
}

.paddingTopProfileElement{
    padding: 8px !important;
    padding-top: 16px !important;
}

.MuiDialogContent-root {
    flex: 1 1 auto;
    padding: 0 24px 24px !important;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

.MuiDialogContent-root.MuiPickersModal-dialog{
    padding: 0px !important;
}

.dialog-title .MuiTypography-root {
    font-size: 21px !important;
    letter-spacing: 0 !important;
    color: #333333 !important;
    opacity: 0.7 !important;
}

.actions-container {
    padding: 0 24px 24px 24px !important;
}

.marginLabelProfileElement {
    padding: 0 8px !important;
    margin-top: 16px !important;
}
