.active-column-cell, .action-column-cell {
    text-align: center;
}

.ReactTable .rt-tbody .rt-td {
    background: #fefefe !important;
}

.green {
    color: green !important;
}

.red {
    color: red !important;
}

.sivotec-page-content {
    padding: 16px;
}

.user-enable {
    font-size: 20px;
    color: #006600 !important;
}

.user-disable {
    font-size: 20px;
    color: #FF6347 !important;
}

.react-app-card-container-users{
    padding: 0px 16px 10px 16px;
    margin: 10px 30px;
}

.stepsContainer .react-app-card-container-users{
    padding: 0 16px 10px 16px;
    margin: 0;
}

.roleChips {
    margin-left: 5px;
    margin-top: 2px !important;
    height: 30px !important;
    background: #E6F2FF 0 0 no-repeat padding-box !important;
    border: 1px solid #1F80F1 !important;
    border-radius: 16px;
    opacity: 1;
}

.roleChips .MuiChip-label {
    font-size: 15px !important;
    letter-spacing: 0;
    color: #333333 !important;
    opacity: 0.8 !important;
}

.specialityChips{
    margin-bottom: 5px;
}

.contactDetails{
    margin-top: 5px;
    cursor: pointer;
    font-size: 18px;
    letter-spacing: 0;
    color: #2C78F8;
    opacity: 0.8;
}

.contactIcons{
    height: 18px;
    width: 18px;
    margin-right: 10px;
}

.practiceCurrency{
    margin-right: 5px;
}

.edit-user-icon {
    border-width: 1px;
    border-style: solid;
    padding: 9px;
    height: 40px !important;
    font-size: 20px !important;
    line-height: 20px !important;
    /* height: 30px; */
    border-color: rgba(112,112,112, 0.2);
    color: #707070;
}

.custom-user-display-name {
    font-size: 18px;
    letter-spacing: 0;
    color: #2A292F;
    opacity: 0.8;
}

.custom-user-name-sub-heading {
    font-size: 14px;
    letter-spacing: 0;
    color: #2A292F;
    opacity: 0.6;
}

.custom-column-heading {
    font-size: 14px;
    letter-spacing: 0;
    color: #333333;
    opacity: 1;
}

.role-chip-icon-container {
    background: #ffffff;
    border-radius: 50%;
    width: 24px !important;
    height: 24px !important;
    padding: 3px 5px !important;
}
