/* Header section CSS start */
.sub-menu {
    right: 0 !important;
    top: 0 !important;
    position: absolute !important;
    color: white !important;
    text-align: center;
    width: 150px !important;
}

.sub-menu-icon {
    font-size: 1.5em !important;
    margin-top: 5px;
}

.sub-menu .ant-menu-submenu-title {
    text-align: right;
}

.ant-menu-submenu-arrow {
    display: none;
}

.ant-menu-item {
    text-align: left;
}

.logo {
    height: 50px;
    background: white;
    width: 168px;
    left: 0;
    position: absolute;
}

.ant-layout-header {
    padding: 0 !important;
}

.dashboard-application-logo-img {
    /* background: white; */
    /* margin: 0 10px 15px 10px; */
