.consult-content {
    background: #ffffff;
}

.consult-grid {
    width: 50%;
    display: inline-block;
    vertical-align: top;
}

.end-consult-button {
    right: 10px !important;
    position: absolute !important;
    background: red !important;
}

.history-consult-content {
    padding-top: 64px;
    padding-left: 24px;
    padding-right: 24px;
}

.section-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 15px;
}

.section-header{
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 15px;
    margin-left: 2%;
}

.video-recording {
    margin: 10px;
    display: inline-block;
    vertical-align: top;
}

.showRecordingButton{
    width: 100% !important;
}

.cardHeader{
    background-color: #F0F0F0;
}

.cardHeaderGridItem{
    padding: 8px !important;
    background-color: #F0F0F0 !important;
    color: #263238 !important;
    font-size: 16px !important;
    font-weight: 600 !important;
    line-height: 20px !important;
    letter-spacing: -0.05px !important;
}

.patientInformationContainer{
    padding: 15px 0px 10px 20px !important;
}
.recordingContainer{
    padding: 15px 12px 10px 12px !important;
}

.attachmentsContainer{
    margin: 15px 0 20px 0 !important;
    padding-right: 12px !important;
    padding-left: 12px !important;
}
.patient-name{
    font-size: 18px;
    font-weight: bold;
    /* margin-top: 5px; */
}

.imageSize{
    margin-left: 5px;
    height: 25px !important;
    width: 30px !important;
    float: right !important;
}

.reasonTitle, .notesTitle{
    font-size: 18px;
    color: gray;
    font-weight: bold;
}

.reasonForVisitContainer{
    padding: 10px 20px 0px 20px !important;
}

.notesContainer{
    padding: 10px 20px 20px 20px !important;
}

.reasonDetails, .notesDetails{
    padding: 10px !important;
    border-radius: 5px;
    background-color: #EEF4FF;
}

.document-title {
    color: #263238;
    font-size: 14px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: -0.05px;
}

.documents-not-available {
    padding-left: 16px;
    margin-top: 8px;
}
