
.ant-layout {
    width: 100% !important;
    height: 100% !important;
    background: white;
}

.ant-layout-sider-children {
    background: white;
}

.page-content {
    background: white;
    margin-top: 50px !important;
    margin-bottom: 35px !important;
}

.ant-layout-footer {
    padding: 0 !important;
}

.save-case button {
    margin: 0 10px;
}

.clickable-span {
    color: #48A5FF;
}

.case-dashboard {
    padding: 10px !important;
}

.case-dashboard .ant-menu-item {
    padding-left: 16px !important;
}

.sivotec-page-content {
    padding: 16px !important;
    background: white;
}

.ReactTable .-previous .-btn {
    background: #1890ff !important;
    color: white !important;
}

.ReactTable .-previous .-btn:disabled {
    background: rgba(0, 0, 0, 0.1) !important;
    color: black !important;
}

.ReactTable .-next .-btn {
    background: #1890ff !important;
    color: white !important;
}

.ReactTable .-next .-btn:disabled {
    background: rgba(0, 0, 0, 0.1) !important;
    color: black !important;
}

.rt-td {
    white-space: normal !important
}

.ReactTable .rt-thead .rt-th.-sort-asc {
    box-shadow: none !important;
}

.ReactTable .rt-thead .rt-th.-sort-desc {
    box-shadow: none !important;
}

.ReactTable .rt-thead .rt-th.-sort-asc div:after {
    content: "\25bc";
    font-size: .7em;
    padding-left: 7px;
    vertical-align: middle;
    box-shadow: none !important;
}

.ReactTable .rt-thead .rt-th.-sort-desc div:after {
    content: "\25B2";
    font-size: .7em;
    padding-left: 7px;
    vertical-align: middle;
    box-shadow: none !important;
}



main.mainContainer{
    margin-top: 48px !important;
}

.drawerHeaderContainer{
    min-height: 48px !important;
}

.sideMenuName{
    text-transform: capitalize !important;
    font-size: 24px !important;
    letter-spacing: 0 !important;
    color: #989898 !important;
    opacity: 1 !important;
}

.connectAccountDetailsVerifedOrNot{
    right: 0px !important;
    position: absolute;
}

.loggedInUserProfileInfoHeader{
    right: 10px !important;
    position: absolute;
    display: inline-flex;
}

.loggedInUserProfileInfoHeader .avatarImage{
    margin-right: 8px;
    box-shadow: 0 0 5px  rgba(0,0,0,0.6);
}

.loggedInUserProfileInfoHeader .userName{
    font-weight: bold;
    line-height: 1.2;
    margin-top: 7px;
    color: #000000DE;
    opacity: 1;
    letter-spacing: 0.21px;
}

.loggedInUserProfileInfoHeader .userRole{
    font-size: 12px !important;
    line-height: 1.2;
    color: #000000DE;
    opacity: 0.6;
}

.verifiedButton{
    margin-left: 5px !important;
}

.contentToolbar{
    min-height: 48px !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
    align-items: flex-start !important;
}

.table-header-title {
    font-size: 21px;
    letter-spacing: 0;
    color: #A2A2A2;
    opacity: 1;
    padding-left: 12px;
}

.table-header-title-container {
    padding: 10px 0 !important;
}

.custom-table-container {
    padding: 0 !important;
}

.custom-table-container .MuiToolbar-gutters {
    padding-left: 0 !important;
}

.doctorSpecialityTextSize{
    font-size: 15px;
}